import {
  ADD_COUNT,
  ADD_TO_CART,
  ADD_USER_INFO,
  ADD_CART_BY_ID
} from './mutation-types'

export default {//用来修改state中的状态   每个方法做的事情尽量单一
  //存储购物车商品信息
  [ADD_CART_BY_ID](state,payload){
    state.cartList = payload
  },
  [ADD_COUNT](state, payload) {
    //商品已存在时 将商品数量+1
    payload.sgcount += 1;
  },
  [ADD_TO_CART](state, payload) {
    
    //商品不存在时  将值放入cartList中
    state.cartList.push(payload)
  },
  //用户登录存储用户信息
  [ADD_USER_INFO](state, payload) {
    state.userInfo = payload;
  }
}