import Vue from 'vue'
import VueRouter from 'vue-router'
//懒加载
const Home = () => import('view/home/Home') 
const Category = () => import('view/category/Category')
const Cart = () => import('view/cart/Cart')
const Profile = () => import('view/profile/Profile')
const Detail = () => import('view/detail/Detail')
const Login = () => import('view/login/Login')
const Collect = () => import('view/collect/Collect')
const ChangePassword = () => import('view/changepassword/ChangePassword')
const UpdloadImg = () => import('view/uploadimg/UpdloadImg')
const Order = () => import('view/order/Order')
const AllOrders = () => import('view/allorders/AllOrders')
const Income = () => import('view/income/Income')
const Manager = () => import('view/manager/Manager')

//1.安装插件
Vue.use(VueRouter)

//2.创建router
const routes = [
  //配置默认路由
  {
    path:'/',
    redirect: '/home'
  },
  { 
    path: '/home', 
    component: Home
  },
  { 
    path: '/cart', 
    component: Cart
  },
  { 
    path: '/category', 
    component: Category
  },
  { 
    path: '/profile', 
    component: Profile
  },
  { 
    path: '/detail/:gid', //传递参数
    component: Detail
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/collect',
    component: Collect
  },
  {
    path: '/changepassword',
    component: ChangePassword
  },
  {
    path: '/uploadimg',
    component: UpdloadImg
  },
  {
    path: '/order',
    component: Order
  },
  {
    path: '/allorders',
    component: AllOrders
  },
  {
    path: '/income',
    component: Income
  },
  {
    path: '/manager',
    component: Manager
  }
];
const router = new VueRouter({
  routes,
  //模式改成history模式
  mode:'hash'
})


//3.导出
export default router