//用来请求购物车的参数
import {request} from './request'

//修改购物车商品的数量  sid  sgcount
export function updShoppingCount(sid,sgcount) {
  return request({
    url: 'updShoppingCount.do',
    method: 'post',
    data:{
      sid,
      sgcount
    }
  }) 
}
//增加购物车中的商品
export function insShoppiing(gid,sgtitle,sgimg,sgsize,sgprice,sgcount,sgstatus,uid) {
  return request({
    url: 'insShoppiing.do',
    method: 'post',
    data: {
      gid,sgtitle,sgimg,sgsize,sgprice,sgcount,sgstatus,uid
    }
  })
}
//获取购物车中商品的sid
export function getShoppingSid(gid, sgsize) {
  return request({
    url: 'getShoppingSid.do',
    method: 'post',
    data: {
      gid,sgsize
    }
  })
}
//修改购物车中的状态
export function updShoppinfStatus(sid,sgstatus) {
  return request({
    url: 'updShoppinfStatus.do',
    method: 'post',
    data: {
      sid,sgstatus
    }
  })
}

//删除购物车中的商品
export function delShoppingById(sid) {
  return request({
    url: 'delShoppingById.do',
    method: 'post',
    data: {
      sid
    }
  })
}