import {
  ADD_COUNT,
  ADD_TO_CART,
  ADD_USER_INFO,
  ADD_CART_BY_ID
} from './mutation-types'
import {updShoppingCount, insShoppiing,getShoppingSid} from 'network/cart'

export default {
  //添加购物车商品信息
  addCartsById(content, payload) {
    return new Promise((resolve, reject) => {
      //提交
      for(let item of payload){
        if(item.sgstatus == "true"){
          item.sgstatus = true
        }else {
          item.sgstatus = false
        }
      }
      
      // console.log(typeof payload.sgstatus);
      content.commit(ADD_CART_BY_ID, payload)
    })
  },
  //添加购物车商品
  addCarts(context, payload) {
    return new Promise((resolve, reject) => {
      //判断添加的商品是否重复
    let oldProduct = null;
    for(let item of context.state.cartList){
      if(item.gid == payload.gid && item.sgsize == payload.sgsize){
        //商品重复时 将数组中的对象赋给oldProduce
        oldProduct = item;
      }
    }
    //判断oldProduce
    if(oldProduct) {
      //发送请求  修改商品数量+1
      let count = oldProduct.sgcount + 1;
      updShoppingCount(oldProduct.sid,count).then(res => {
         
      }) 
      context.commit(ADD_COUNT, oldProduct)
      resolve('当前商品数量+1')
    }else {
      //赋值
      payload.sgcount = 1;
      payload.sgstatus = true;
      //发送请求  增加购物车中的商品  gid,sgtitle,sgimg,sgsize,sgprice,sgcount,sgstatus,uid
      insShoppiing(payload.gid,payload.sgtitle,payload.sgimg,payload.sgsize,payload.sgprice,payload.sgcount,payload.sgstatus,payload.uid).then(res => {
         //发送请求根据商品id和商品大小获取购物车商品的sid
          getShoppingSid(payload.gid,payload.sgsize).then(res => {
            payload.sid = res
          })
      })
      context.commit(ADD_TO_CART, payload)
      resolve('添加成功')
    }
    })
    
  },
  //添加用户信息
  addUserInfo(content, payload) {
    return new Promise((resolve, reject) => {
      content.commit(ADD_USER_INFO, payload)
      resolve('登录成功')
    })
  }
}