import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

//安装插件
Vue.use(Vuex)

//创建store对象
const state = {
  //存放用户信息
  userInfo: {
    uid:0,
    idadmin: 'false'
  },
  //存放购物车中商品
  cartList: [],
  //存放收藏的信息
  
}
//创建store对象
const store = new Vuex.Store({
  state,
  mutations,
  actions
})

//挂载到vue实例上
export default store