<template>
  <div id="tab-bar-item" @click="itemClick">
    <!--  将三个插槽分别用来放图片    激活的图片    文字-->
    <div class="item-icon" v-show="isActive">
      <slot name="icon"></slot>
    </div>
    <div class="item-active-icon" v-show="!isActive">
      <slot name="active-icon"></slot>
    </div>
    <div class="item-text" :style="isActiveText">
      <slot name="text"></slot>
    </div>
  </div>
</template>

<script>
  //导航栏里一个小组件
  export default {
    name: 'TabBarItem',
    props: {
      //路由跳转的路径
      link: {
        //类型为String 必填
        type: String,
        required: true
      },
       //动态修该字体颜
       activeColor: {
         type: String,
         default: 'red'
       }
    },
    computed: {
      //判断图片显示活跃或不活跃  
      isActive() {
        //当前路由和内容不同时  为true
        return this.$route.path.indexOf(this.link) == -1;
      },
      //判断文字是否活跃
      isActiveText() {
        return this.isActive ?{} : {'color': this.activeColor}
      }
    },
    methods: {
      itemClick() {
        //通过点击事件来监听路由跳转
        this.$router.replace(this.link)
      }
    }
  }
</script>

<style scoped>
  #tab-bar-item{
    /*让所有弹性盒模型的子元素都拥有相同的长度，且忽视内部内容 */
    flex: 1;
  }
  /*图片的样式*/
  .item-icon img, .item-active-icon img{
    height:24px;
    width: 24px;
    margin-top: 5px;
    /*单元格内容垂直对齐方式 */
    vertical-align: middle;
  }
  /*文字的样式 */
   .item-text {
    font-size: 12px;
    margin-top: 3px;
    color: #333;
  }
</style>