<template>
  <div class="toast" v-show="isShow">
    <div>{{message}}</div>
  </div>
</template>

<script>
  export default {
    name: 'Toast',
    data () {
      return {
        message: '',
        isShow: false
      }
    },
    methods: {
      show(message, duration = 2000) {//传入两个参数 文字 的内容 和显示的时间
        this.isShow = true;
        this.message = message;
        setTimeout(() => {
          this.isShow = false;
          this.message = ''
        }, duration)
      }
    }
  }
</script>

<style scoped>
  .toast{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 8px 10px;
    z-index: 100;
    color: #ffffff;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.7)
  }
</style>